import { Base, createApp, importWhenVisible } from "@studiometa/js-toolkit";
import { Figure } from "@studiometa/ui";
import { isDev } from "./config.js";
class App extends Base {
  mounted() {
    this.$log("mounted \u{1F389}");
  }
}
App.config = {
  log: isDev(),
  name: "App",
  components: {
    Figure,
    Video: (app) => importWhenVisible(() => import("./molecules/Video.js"), "Video", app),
    Accordion: (app) => importWhenVisible(() => import("./molecules/Accordion.js"), "Accordion", app)
  }
};
var app_default = createApp(App, document.body);
export {
  app_default as default
};


if (module.hot) {module.hot.accept(function(err) {
if (err) {
console.error(err);
}
});
}
